.menu-wrapper{display:flex;flex-wrap:wrap;justify-content:space-between;padding:0 20px;margin-top:50px}.menu-wrapper .icon-loading{margin:0 auto}.menu-item{position:relative;width:32%;margin-bottom:20px;box-shadow:0 1px 6px 0 rgba(32,33,36,0.28);padding:20px 20px 10px 20px}@media (max-width: 1200px){.menu-item{width:48%}}@media (max-width: 992px){.menu-item{width:100%}}.menu-item.is-disabled::after{opacity:0.4;display:block;position:absolute;top:0;left:0;width:100%;height:100%;background:#797474;z-index:100;content:''}.menu-item.is-disabled:hover .icon-add{display:none !important}.menu-item:hover .icon-add{display:block !important}.menu-item:hover .img-dish{transform:scale(1.5);opacity:0.5}.menu-item .image{overflow:hidden;height:calc(100% - 50px);position:relative}.menu-item .image .img-dish{width:100%;height:100%;transition:transform ease 0.5s}.menu-item .image .icon-add{display:none;width:30px;height:30px;position:absolute;top:50%;left:50%;transform:translate(-50%, -50%);cursor:pointer}.menu-item .desc{display:flex;justify-content:space-between;align-items:center;margin-top:8px}.menu-item .info{display:flex;flex-direction:column;align-items:flex-start}.menu-item .name{font-weight:600;font-size:16px;max-height:25px;overflow:hidden}.menu-item .price{font-style:italic;color:#0e78ff}.menu-item .quantity input{width:45px;border:1px solid #333;text-align:center;font-size:16px}@-webkit-keyframes xAxis{100%{transform:translateX(calc(50vw - 105px))}}@keyframes xAxis{100%{transform:translateX(calc(50vw - 105px))}}@-webkit-keyframes yAxis{100%{transform:translateY(calc(-50vh + 75px))}}@keyframes yAxis{100%{transform:translateY(calc(-50vh + 75px))}}@-webkit-keyframes shakeCart{25%{transform:translateX(6px)}50%{transform:translateX(-4px)}75%{transform:translateX(2px)}100%{transform:translateX(0)}}@keyframes shakeCart{25%{transform:translateX(6px)}50%{transform:translateX(-4px)}75%{transform:translateX(2px)}100%{transform:translateX(0)}}

.cart-wrapper{position:fixed;top:20px;right:20px;width:60px;z-index:1000}.cart-icon{cursor:pointer}.cart-icon.shake{-webkit-animation:shakeCart 0.3s ease-in-out forwards;animation:shakeCart 0.3s ease-in-out forwards}.cart-icon img{width:60px}.cart-loading{position:absolute;width:100%;height:100%;top:0;right:0;opacity:0.5;z-index:1000}.cart-qty{position:absolute;top:-10px;right:30px;font-weight:600;font-size:18px;color:red}.cart-content{display:none;flex-direction:column;position:absolute;top:60px;right:0;background:#fff;box-shadow:0 1px 6px 0 rgba(32,33,36,0.28);min-width:300px;padding:10px;z-index:100;border-radius:8px}.cart-content.show{display:flex}.cart-content .btn-order{background:#51decc;box-shadow:none;border:1px solid #12b18c;width:80px;padding:5px 0;color:#333;font-weight:600;border-radius:4px;outline:none;margin:10px auto 0}.cart-item{display:flex;justify-content:space-between;border-bottom:1px solid #dddddd;padding:5px 0;font-size:16px}.cart-item:last-child{border:0}.cart-item .name{color:#0e78ff}.cart-item .qty{color:red}.cart-item .desc{display:flex;width:50px;align-items:normal;justify-content:space-around}.cart-item .remove{cursor:pointer;display:flex;align-items:center}.cart-item .remove img{width:12px;height:12px}@-webkit-keyframes shake{0%{transform:translate(1px, 1px) rotate(0deg)}10%{transform:translate(-1px, -2px) rotate(-1deg)}20%{transform:translate(-3px, 0px) rotate(1deg)}30%{transform:translate(3px, 2px) rotate(0deg)}40%{transform:translate(1px, -1px) rotate(1deg)}50%{transform:translate(-1px, 2px) rotate(-1deg)}60%{transform:translate(-3px, 1px) rotate(0deg)}70%{transform:translate(3px, 1px) rotate(-1deg)}80%{transform:translate(-1px, -1px) rotate(1deg)}90%{transform:translate(1px, 2px) rotate(0deg)}100%{transform:translate(1px, -2px) rotate(-1deg)}}@keyframes shake{0%{transform:translate(1px, 1px) rotate(0deg)}10%{transform:translate(-1px, -2px) rotate(-1deg)}20%{transform:translate(-3px, 0px) rotate(1deg)}30%{transform:translate(3px, 2px) rotate(0deg)}40%{transform:translate(1px, -1px) rotate(1deg)}50%{transform:translate(-1px, 2px) rotate(-1deg)}60%{transform:translate(-3px, 1px) rotate(0deg)}70%{transform:translate(3px, 1px) rotate(-1deg)}80%{transform:translate(-1px, -1px) rotate(1deg)}90%{transform:translate(1px, 2px) rotate(0deg)}100%{transform:translate(1px, -2px) rotate(-1deg)}}

.countdown{text-align:center}.countdown span{position:relative;display:inline-block;font-size:30px;color:#fff;display:inline-block;padding:5px;background:#4d9ef5;margin-right:10px;width:48px;text-align:center;border-radius:4px}.countdown span:last-child::after{display:none}.countdown span:after{color:#333;position:absolute;top:0;right:-10px;content:':'}.countdown-message{font-size:28px;font-weight:600;color:#cb3e3e;margin:0}.timeout-icon{width:250px;height:250px}.timeout-message{margin-top:50px;font-size:20px;text-align:center;font-weight:600}.timeout-message .note{color:#b53e3e;font-weight:600}.timeout-modal .modal-content{height:80vh;width:80vw;background:#333;--border-width: 5px;position:relative;display:flex;border-radius:var(--border-width)}.timeout-modal .modal-content::after{position:absolute;content:'';top:calc(-1 * var(--border-width));left:calc(-1 * var(--border-width));z-index:-1;width:calc(100% + var(--border-width) * 2);height:calc(100% + var(--border-width) * 2);background:linear-gradient(60deg, #5f86f2, #a65ff2, #f25fd0, #f25f61, #f2cb5f, #abf25f, #5ff281, #5ff2f0);background-size:300% 300%;background-position:0 50%;border-radius:calc(2 * var(--border-width));-webkit-animation:moveGradient 4s alternate infinite;animation:moveGradient 4s alternate infinite}.timeout-modal__body{display:flex;flex-direction:column;align-items:center;justify-content:center;padding:20px 40px;color:#fff}@-webkit-keyframes moveGradient{50%{background-position:100% 50%}}@keyframes moveGradient{50%{background-position:100% 50%}}

.order-wrapper{margin-top:50px;margin:20px 0 50px}.order-total{width:500px;margin-bottom:20px}.order-total .title{border:1px solid #333;padding:10px 0;text-align:center;margin:0;border-bottom:0;color:#fff;background:#2f6d8c}.order-total .total{display:flex;justify-content:space-between;border:1px solid #333;padding:10px 15px;text-align:right;font-size:20px;font-weight:600;color:red}.order-total .item{display:flex;border:1px solid #333;border-bottom:0;box-sizing:border-box;height:40px}.order-total .item:last-child{border-bottom:1px solid #333}.order-total .item span{border-right:1px solid #333;width:50%;height:100%;align-items:center;padding:0 15px;display:flex;font-size:14px;font-weight:600;text-align:center}.order-total .item span:last-child{border-right:0;justify-content:flex-end;color:red}.order-content{box-shadow:0 1px 6px 0 rgba(32,33,36,0.28);padding:20px}.order-item{display:flex;font-size:16px;justify-content:space-between;align-items:center;border-bottom:1px solid #ddd;padding:10px 5px}.order-item__title{display:flex;font-size:18px;font-weight:600;justify-content:space-between;align-items:center;color:#fff;padding:10px 5px;background-color:#2f6d8c}.order-item__title span{width:25%;text-align:center}.order-item__title span:nth-child(2){width:20%}.order-item__title .paid{width:10%}.order-item__date-title{text-align:center;font-size:16px;font-weight:600;background:#2e4c5d;color:#fff;padding:10px 0}.order-item:nth-child(2n+1){background:#eaeaea}.order-item:last-child{border-bottom:0}.order-item span{width:25%;text-align:center}.order-item span:first-child{text-align:left}.order-item .quantity{width:20%;text-align:center}.order-item .paid{width:10%;text-align:center;position:relative;height:25px}.order-item .paid .check-mask{position:absolute;top:0;right:30px;width:25px;height:25px;display:inline-block;border:1px solid #333;border-radius:2px;content:''}.order-item .paid input[type='checkbox']{position:absolute;top:0;right:30px;width:25px;height:25px;opacity:0;z-index:10}.order-item .paid input[type='checkbox']:checked+.check-mask{background-image:url(/static/media/tick.db93b3b8.svg);border-color:green;background-size:cover}

